

import { dirMapStore, userInfoStore, fileListStore } from "@/store";

export default {
  setup() {

    const activeFolder = (idx: number) => {
      dirMapStore.directories.forEach((e, i) => {

          if(i === idx && ( ! e.is_opened)) {
            e.is_opened = true
            e.icon = 'folder-open'
          } else if(i === idx && e.is_opened) {
            e.is_opened = false
            e.icon = 'folder'
          }

          if(i !== idx) {
            e.is_opened = false
            e.icon = 'folder'
          }
      })
    }

    return {
      userInfoStore,
      dirMapStore,
      fileListStore,
      activeFolder,
      rendered: {
        latest_index: 0,
        postList: []
      }
    }
  }
}
